// 店铺账户相关接口
import axios from '../fetch'

// 店铺账户列表（分页）
export const accountList = params => { return axios.get('/admin/platform/storeAccount/page', { params: params }) }
// 店铺账户明细列表（分页）
export const detailPage = params => { return axios.get('/admin/platform/storeAccount/detailPage', { params: params }) }
// 提交资金调整
export const accountUpdate = params => { return axios.post('/admin/platform/storeAccount/update', params) }
// 账户列表提现分页
export const stoPrePage = params => { return axios.get('/admin/platform/storeAccount/stoPrePage', { params: params }) }
// 审核提现账户
export const stoPreAudit = params => { return axios.get('/admin/platform/storeAccount/audit', { params: params }) }
// 删除提现账户
export const stoPreDelete = params => { return axios.get('/admin/platform/storeAccount/delete', { params: params }) }
// 资金调整
export const accountSelectGet = params => { return axios.get('/admin/platform/storeAccount/select', { params: params }) }
// 店铺账户信息
export const storeAccountGet = params => { return axios.get('/admin/store/storeAccountInfo/get', { params: params }) }
// 店铺账户明细列表
export const storeAccountDetailpage = params => { return axios.get('/admin/store/storeAccountInfo/detailpage', { params: params }) }
// 店铺账户提现列表
export const storeAccountPresentpage = params => { return axios.get('/admin/store/storeAccountInfo/presentpage', { params: params }) }
// 店铺账户保证金详细
export const storeAccountBondDetails = params => { return axios.get('/admin/store/storeAccountInfo/bondDetails', { params: params }) }
// 店铺账户-充值
export const storeAccountRecharge = params => { return axios.get('/admin/store/storeAccountInfo/recharge', { params: params }) }
// 店铺账户-退回服务
export const storeAccountQuit = params => { return axios.get('/admin/store/storeAccountInfo/quit', { params: params }) }
// 店铺账户-退回余额
export const storeAccountSendback = params => { return axios.get('/admin/store/storeAccountInfo/sendback', { params: params }) }
// 店铺账户-退回余额
export const storeAccountSupplement = params => { return axios.post('/admin/store/storeAccountInfo/supplement', params) }
// 店铺账户-提现
export const storeAccountPresent = params => { return axios.post('/admin/store/storeAccountInfo/present', params) }




