<template>
  <div>
    <div>
      <!-- 数据列表 -->
      <el-table :data="tableData" style="width:100%" stripe v-loading="loading" ref="dataTable">
        <el-table-column prop="name" label="项目" min-width="180">
          <template slot-scope="scope">
            <div
              class="bold"
            >{{scope.row.isCategory?'主营类目（'+scope.row.name+'）':'保障服务（'+scope.row.name+'）'}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="isCategory" label="说明" min-width="210">
          <template slot-scope="scope">
            <div class="t-label" v-if="scope.row.isCategory">
              <p>退回主营类目保证金视同注销店铺，</p>
              <p>系统将暂停店铺营业，请谨慎操作</p>
            </div>
            <div class="t-label " v-else>
              <p>退回此项保障服务，相关商品将不再展示相应保障服务</p>
            </div>
          </template>
        </el-table-column>
        <el-table-column prop="oldPrice" label="金额" min-width="380">
          <template slot-scope="scope">
            <p class="bold">￥{{$cms.float(scope.row.oldPrice)}}</p>
            <p class="t-label" v-if="scope.row.status>0" :class="scope.row.status==2?'green':'red'">
              （此项保证金最新调整为¥{{$cms.float(scope.row.newPrice)}}，
              <span
                v-if="scope.row.status == 2"
              >可将差额转至账户余额</span>
              <span v-else>请在{{scope.row.difference}}天（{{scope.row.finalDate}}）内补交差额</span> ）
            </p>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="210" fixed="right" align="center">
          <template slot-scope="scope">
            <el-button v-if="scope.row.status==2" type="text" @click="turnBalance(scope.row)">转至余额</el-button>
            <el-button v-if="scope.row.status==1" type="text" @click="make(scope.row)">补交</el-button>
            <el-button type="text" @click="backAccount(scope.row.id)">退回</el-button>
          </template>
        </el-table-column>
      </el-table>
      <!-- 添加弹窗 -->
      <el-dialog
        title="保证金差额补交"
        :visible="makeVisible"
        width="640px"
        :close-on-click-modal="false"
        :close-on-press-escape="false"
        :before-close="handleClose"
      
      >
        <div class="dialog-form"   v-loading="payLoading">
          <cms-prompt collapse="true">
            <li>系统会自动从账户余额中扣减，请确保账户余额内的资金充足！</li>
          </cms-prompt>
          <el-form :model="makeForm" ref="dialogForm">
            <el-form-item label="费用名称：" label-width="120px">
              <span>{{makeForm.name}}</span>
            </el-form-item>
            <el-form-item label="充值账户：" label-width="120px">
              <span>{{makeForm.account}}</span>
            </el-form-item>
            <el-form-item label="金额：" label-width="120px">
              <span>{{makeForm.price}}</span>
            </el-form-item>
            <el-form-item
              label="支付密码："
              label-width="120px"
              prop="pStr"
              :rules="
            [{ required: true, message: '请输入支付密码', trigger: 'blur' }]"
            >
              <el-input
                v-model="makeForm.pStr"
                type="password"
                :rows="4"
                placeholder="请输入支付密码"
                maxlength="16"
              ></el-input>
            </el-form-item>
            <div class="center">
              <cms-button @click="makeBond(makeForm)" style="margin-right:10px;">确认</cms-button>
              <cms-button @click="handleClose" type="info">取消</cms-button>
            </div>
          </el-form>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import list from '@/mixins/list'
import {
  storeAccountBondDetails,
  storeAccountQuit,
  storeAccountSendback,
  storeAccountSupplement
} from '@/api/shop/shopAccount.js'

export default {
  mixins: [list],
  data() {
    return {
      makeVisible: false,
      payLoading: false,
      makeForm: {
        name: '',
        account: '',
        price: '',
        pStr: '',
        id: ''
      }
    }
  },
  methods: {
    makeBond(data) {
      // 补交保证金
      this.$refs.dialogForm.validate(valid => {
        if (valid) {
          const params = {
            id: data.id,
            pStr: this.$cms.encrypt(data.pStr)
          }
          this.payLoading = true
          storeAccountSupplement(params).then(res => {
            this.payLoading = false
            if (res.code === 200) {
              this.$successMessage('操作成功')
              this.handleClose()
              this.getTableData(storeAccountBondDetails, {}, false)
            } else {
              this.$errorMessage(res.message)
              this.handleClose()
            }
          })
        }
      })
    },
    make(row) {
      this.makeVisible = true
      this.makeForm = {
        name: row.name + '保证金差额',
        account: row.account,
        price: '￥' + this.$cms.float(row.newPrice - row.oldPrice),
        pStr: '',
        id: row.id
      }
    },
    handleClose() {
      // 关闭弹窗
      this.makeVisible = false
    },
    turnBalance(row) {
      // 退回余额
      this.$confirm(
        '当前保证金为¥' +
          this.$cms.float(row.newPrice) +
          '，您所缴纳的此项保证金存在¥' +
          this.$cms.float(row.oldPrice - row.newPrice) +
          '的余额差，是否确认转至账户余额？',
        '系统提示',
        {
          confirmButtonText: '确认',
          cancelButtonText: '取消',
          type: 'warning'
        }
      )
        .then(res => {
          storeAccountSendback({ id: row.id }).then(res => {
            if (res.code === 200) {
              this.$successMessage('操作成功')
              this.getTableData(storeAccountBondDetails, {}, false)
            } else {
              this.$errorMessage(res.message)
            }
          })
        })
        .catch(res => {
          return false
        })
    },
    backAccount(id) {
      // 退回服务
      this.$confirm(
        '退回保证金，店铺不再享有相应服务（如退回的是主营类目保证金，系统将暂停店铺营业），相应金额将退回至账户余额内',
        '系统提示',
        {
          confirmButtonText: '我已了解风险，确认执行',
          cancelButtonText: '取消',
          type: 'warning'
        }
      )
        .then(res => {
          storeAccountQuit({ id: id }).then(res => {
            if (res.code === 200) {
              this.$successMessage('操作成功')
              this.getTableData(storeAccountBondDetails, {}, false)
            } else {
              this.$errorMessage(res.message)
            }
          })
        })
        .catch(res => {
          return false
        })
    }
  },
  created() {
    this.getTableData(storeAccountBondDetails, {}, false)
  }
}
</script>
<style lang="scss" scoped>
.cms-list-query {
  padding-top: 0;
  padding-bottom: 3px;
}
.bold {
  font-weight: bold;
}
.green {
  color: green;
}
.red {
  color: red;
}

.t-label {
    font-size: 12px;
  color: #999;
  line-height: 1.5;
}
</style>



