<template>
  <div class="cms-list-layout" v-loading="loading">
    <ul class="account-wrap__head" >
      <li class="account-wrap__item t-item1">
        <div class="account-wrap__image">
          <img class="account-wrap__img" src="@/assets/images/account03.png" alt>
        </div>
        <div class="account-wrap__cont">
          <p class="account-wrap__title">账户余额</p>
          <p class="account-wrap__price">
            <span class="account-wrap__wid">￥
              <b class="account-wrap__bold">{{getPoint(info.balance)}}</b>
              {{getSpot(info.balance)}}
            </span>
            <a @click="rechargeType(1)" class="account-wrap__link t-link">充值</a>
            <a @click="activeName = 'fourth'" class="account-wrap__link">提现</a>
          </p>
        </div>
      </li>
      <li class="account-wrap__item t-item2">
        <div class="account-wrap__image">
          <img class="account-wrap__img" src="@/assets/images/account02.png" alt>
        </div>
        <div class="account-wrap__cont">
          <p class="account-wrap__title">冻结金额</p>
          <p class="account-wrap__price">
            <span class="account-wrap__wid">￥
              <b class="account-wrap__bold">{{getPoint(info.frozenMoney)}}</b>
              {{getSpot(info.frozenMoney)}}
            </span>
          </p>
        </div>
      </li>
      <li class="account-wrap__item t-item3">
        <div class="account-wrap__image">
          <img class="account-wrap__img" src="@/assets/images/account01.png" alt>
        </div>
        <div class="account-wrap__cont">
          <p class="account-wrap__title">保证金</p>
          <p class="account-wrap__price">
            <span class="account-wrap__wid">￥
              <b class="account-wrap__bold">{{getPoint(info.bondMoney)}}</b>
              {{getSpot(info.bondMoney)}}
            </span>
            <a @click="rechargeType(2)" class="account-wrap__link t-link">明细</a>
          </p>
        </div>
      </li>
    </ul>
    <div>
      <el-tabs v-model="activeName">
        <el-tab-pane label="账户明细" name="first">
          <account-detailed ref="detailed" @change="eventChange"></account-detailed>
        </el-tab-pane>
        <el-tab-pane label="提现记录" name="second">
          <account-record ref="record" @change="eventChange"></account-record>
        </el-tab-pane>
        <el-tab-pane v-if="activeName === 'third'" label="账户充值" name="third">
          <recharge :retype="retype" :price="price"></recharge>
        </el-tab-pane>
        <el-tab-pane v-if="activeName === 'fourth'" label="账户提现" name="fourth">
          <withdrawal :price="info.balance"></withdrawal>
        </el-tab-pane>
        <el-tab-pane v-if="activeName === 'five'" label="保证金明细" name="five">
          <bond-detail></bond-detail>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>

<script>
import accountDetailed from './accountDetailed'
import accountRecord from './accountRecord'
import recharge from './recharge'
import withdrawal from './withdrawal'
import bondDetail from './bondDetail'
import { storeAccountGet } from '@/api/shop/shopAccount.js'
export default {
  name: 'shopAccountLayOut',
  components: {
    accountDetailed,
    accountRecord,
    recharge,
    withdrawal,
    bondDetail
  },
  data() {
    return {
      activeName: 'first',
      retype: '',
      loading: false,
      info: {
        balance: 0,
        bondMoney: 0,
        frozenMoney: 0
      },
      price: 0
    }
  },
  methods: {
    rechargeType(type) {
      // 充值
      if (type === 1) {
        this.retype = '账户余额'
        this.activeName = 'third'
        this.price = this.info.balance
      } else {
        this.activeName = 'five'
      }
    },
    eventChange(ev) {
      // 切换时加载列表
      // 出售中列表状态更新
      if (ev === 'detailed') {
        if (this.$refs['detailed']) {
          this.$refs['detailed'].changeData() // 调用列表更新
        }
      }
      if (ev === 'record') {
        if (this.$refs['record']) {
          this.$refs['record'].changeData() // 调用列表更新
        }
      }
    },
    getInfo() {
      // 获取当前账户信息
      this.loading = true
      storeAccountGet().then(res => {
        if (res.code === 200) {
          this.info = res.data
          this.loading = false
        } else {
          this.$errorMessage(res.code + res.message)
          this.loading = false
        }
      })
    },
    getPoint(val) {
      var s = parseInt(val).toFixed(0)
      return s
    },
    getSpot(val) {
      var s = String(val)
      var i = s.indexOf('.')
      if (i > 0) {
        var t = s.slice(i)
        if (t.length === 3) {
          return t
        } else if (t.length === 2) {
          return t + '0'
        } else {
          return t.slice(0, 2)
        }
      } else {
        return '.00'
      }
    }
  },
  activated() {
    this.getInfo()
  },
  created() {
    if (this.$route.query.active === 5) {
      this.activeName = 'five'
    }
    this.getInfo()
  }
}
</script>

<style lang="scss" scoped>
.account-wrap {
  width: 100%;
  &__head {
    max-width: 1060px;
    margin: 0px auto;
  }
  &__item {
    width: 300px;
    height: 125px;
    padding: 28px 5px;
    box-sizing: border-box;
    border-radius: 10px;
    display: inline-block;
    margin: 20px 20px;
    overflow: hidden;
  }
  &__title {
    line-height: 28px;
  }
  &__image {
    width: 65px;
    height: 65px;
    // background-color: #f5f5f5;
    display: inline-block;
    margin-right: 5px;
    vertical-align: top;
    text-align: center;
    line-height: 75px;
  }
  &__img {
    max-width: 100%;
    max-height: 100%;
  }
  &__cont {
    display: inline-block;
    vertical-align: top;
    font-size: 16px;
    font-weight: bold;
    width: 210px;
  }
  &__link {
    font-size: 14px;
    font-weight: bold;
    width: 15%;
    text-align: center;
    display: inline-block;
    vertical-align: top;
    line-height: 45px;
  }
  &__bold {
    font-size: 22px;
    line-height: 42px;
    font-weight: 600;
  }
  &__price {
    letter-spacing: -1px;
    overflow: hidden;
    height: 48px;
    box-sizing: border-box;
    width: 100%;
  }
  &__wid {
    max-width: 60%;
    padding-right: 3px;
    overflow: hidden;
    text-overflow: ellipsis;
    -webkit-line-clamp: 1;
    display: inline-block;
  }
}
.t-link {
  color: #1296db;
}
.t-item1 {
  border: 1px solid rgba(18, 150, 219, 1);
  box-shadow: 1px 1px 5px rgba(18, 150, 219, 1);
}
.t-item2 {
  border: 1px solid rgba(221, 144, 67, 1);
  box-shadow: 1px 1px 5px rgba(221, 144, 67, 1);
}
.t-item3 {
  border: 1px solid rgba(78, 140, 238, 1);
  box-shadow: 1px 1px 5px rgba(78, 140, 238, 1);
}
</style>


