<template>
  <div>
    <div>
      <!-- 筛选区 -->
      <section class="cms-list-query">
        <div class="query-group">
          <label>资金类型</label>
          <el-select v-model="queryParams.capitalChangeType" @change="query" placeholder="请选择">
            <el-option label="全部" :value="''"></el-option>
            <el-option label="账户金额" :value="1"></el-option>
            <el-option label="保证金" :value="2"></el-option>
            <el-option label="冻结金额" :value="3"></el-option>
          </el-select>
        </div>
        <div class="query-group">
          <label>资金变更方式</label>
          <el-select v-model="queryParams.calculatorType" @change="query" placeholder="请选择">
            <el-option label="全部" :value="''"></el-option>
            <el-option label="增加" :value="1"></el-option>
            <el-option label="减少" :value="2"></el-option>
          </el-select>
        </div>
        <div class="query-group">
          <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
        </div>
      </section>
      <!-- 数据列表 -->
      <el-table :data="tableData" style="width: 100%" stripe   ref='dataTable'>
      
        <el-table-column prop="businessNo" label="编号" min-width="160"></el-table-column>
        <el-table-column prop="capitalChangeType" :formatter="typeFilter" label="资金类型" min-width="100"  align="center"></el-table-column>
        <el-table-column prop="createTime" label="调整时间"  min-width="160" align="center">
          <template slot-scope="scope">
            {{$moment(scope.row.createTime)}}
          </template>
        </el-table-column>
        <el-table-column prop="remark" label="备注" min-width="160"  align="center">
          <template slot-scope="scope">
            <div class="text-overflow" :title="scope.row.remark">{{scope.row.remark}}</div>
          </template>
        </el-table-column>
        <el-table-column prop="money" label="调整金额"  align="center">
          <div slot-scope="scope">
           <span :class='[scope.row.calculatorType === 1?"add":"desc"]'>{{$cms.float(scope.row.money)}}</span>
          </div>
        </el-table-column>
        <el-table-column prop="beforeMoney" label="调整前的金额"  align="center">
          <div slot-scope="scope">
            {{$cms.float(scope.row.beforeMoney)}}
          </div>
        </el-table-column>
      </el-table>
      <cms-pagination @change="getPage" :total='total'></cms-pagination>

    </div>
  </div>
</template>

<script>
var defaultData = {
  page: 1,
  size: 10,
  capitalChangeType: '',
  calculatorType: ''
}
import list from '@/mixins/list'
import { storeAccountDetailpage } from '@/api/shop/shopAccount.js'
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    typeFilter(row, column) {
      // console.log(row.capitalChangeType)
      var types = ['', '账户金额', '保证金', '冻结金额']
      return types[parseInt(row.capitalChangeType)]
    },
    changeData() { // 通过ref来触发
      this.getTableData(storeAccountDetailpage, this.queryParams)
    }
  },
  activated() {
    this.getTableData(storeAccountDetailpage, this.queryParams)
  },
  created() {
    this.getTableData(storeAccountDetailpage, this.queryParams)
  }
}
</script>

<style lang="scss" scoped>
  .cms-list-query{
    padding-top: 0;
    padding-bottom: 3px;
  }
  .add{
    color: green;
    position: relative;
  }
  .add::before{
    content: '+';
    color: green;
    position: absolute;
    left: -8px;
  }
  .desc{
    color: red;
    position: relative;
  }
  .desc::before{
    content: '-';
    color: red;
    position: absolute;
    left: -8px;
  }
</style>



