<template>
  <div >
      <cms-prompt collapse='true'>
        <li>可提现金额最大为当前账户的可用余额</li>
        <li>提现金额必须为大于10且小于50000的整数</li>
        <li>提现申请会在1~7日内由系统审核并转账</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px">
        <el-form-item label="账户余额">
          <p>￥{{$cms.float(price)}}</p>
        </el-form-item>
        <el-form-item label="提现金额" prop="amount">
          <el-input v-model="formData.amount" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="收款账户" prop="settlementId">
          <ul class="user-account__list">
            <li class="user-account__item" v-for="(item,index) in accountData" :key="index" 
            @click="activeTab(item.id)"
            :class="item.id === formData.settlementId?'user-account__item--active':''">
              <span v-show="item.isDefault" class="user-account__defult fr">默认</span>
              <p class="user-account__txt">
                <label>账户类型：</label>
                <span>{{item.accountType | codeFilter}}</span>
              </p>
              <p class="user-account__txt">
                <label>账户：</label>
                <span>{{item.account}}</span>
              </p>
              <p class="user-account__txt">
                <a @click="getOneInfo(item.id)" href="#">
                  <i class="el-icon-edit fr"></i>
                </a>
                <label>姓名：</label>
                <span>{{item.accountName}}</span>
              </p>
            </li>
            <li v-if="isSave" @click="editAccount(userInfo)" class="user-account__item user-account__nomal">
              <span class="user-account__add">+</span>
            </li>
          </ul>
        </el-form-item>
        <el-form-item label="支付密码" prop="payPassWord">
          <el-input type="password" v-model="formData.payPassWord" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="withdraw"  :disabled="formData.amount<10 || formData.payPassWord ==''" v-color>提现</cms-button>
      </div>
      <el-dialog width="650px" :title="'收款账户 '+(type)" :visible.sync="accountDialog" :before-close="done">
        <el-form :model="userInfo" :rules="rules" ref="account" style="padding:10px">
          <el-form-item label="收款类型" label-width="110px" prop="type">
            <el-radio-group v-model="userInfo.accountType">
              <el-radio :label="1">支付宝</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="收款账户" label-width="110px" prop="account">
            <el-input v-model="userInfo.account" maxlength="30"></el-input>
          </el-form-item>
          <el-form-item label="账户姓名" label-width="110px" prop="accountName">
            <el-input v-model="userInfo.accountName" maxlength="12"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer">
          <el-button size="mini" @click="done">取 消</el-button>
          <el-button size="mini" type="primary" @click="saveAccount">确 定</el-button>
        </div>
      </el-dialog>
      
    </div>
</template>

<script>
import { getAccountList, saveSettleAccount, settleNumber, updateSettleAccount, getAccountOne } from '@/api/shop/shopInfo.js'
import { storeAccountPresent } from '@/api/shop/shopAccount.js'
import rules from '@/rules'
import form from '@/mixins/form.js'
var defaultData = {
  settlementId: '',
  amount: '',
  payPassWord: ''
}
var userData = {
  'accountType': 1,
  'account': '',
  'accountName': '',
  'isDefault': false
}
var validateAccount = (rule, value, callback) => {
  const reg = /![\u4e00-\u9fa5]+/
  const reg1 = /^[?=\x21-\x7eA-Za-z0-9]+[?=\x21-\x7eA-Za-z0-9\s]*[?=\x21-\x7eA-Za-z0-9]+$/
  if (value === '') {
    callback(new Error('请输入收款账户'))
  } else if (!reg.test(value) && !reg1.test(value)) {
    callback(new Error('账户格式不正确'))
  } else {
    callback()
  }
}
var validateName = (rule, value, callback) => {
  const reg = /^[\u4E00-\u9FA5A-Za-z]+$/
  if (value === '') {
    callback(new Error('请输入账户姓名'))
  } else if (!reg.test(value)) {
    callback(new Error('账户姓名格式不正确'))
  } else {
    callback()
  }
}
export default {
  mixins: [form],
  props: {
    price: {
      type: [String, Number],
      default: 0.00
    }
  },
  data() {
    var isAmount = (rule, value, callback) => {
      if (parseInt(value) < 10 || parseInt(value) > 50000) {
        callback(new Error('请输入10~50000元之间数额'))
      } else if (parseInt(value) > this.price) {
        callback(new Error('提现金额不能大于当前余额'))
      } else {
        callback()
      }
    }
    return {
      type: '新增',
      accountDialog: false,
      formData: Object.assign({}, defaultData),
      userInfo: Object.assign({}, userData),
      rules: {
        // 充值规则
        payPassWord: [rules.required('请填写支付密码'), rules.len(6, 16)],
        accountTypeCode: [rules.required('请填写收款类型')],
        account: [rules.required('请填写收款账户'), { validator: validateAccount, trigger: 'blur' }],
        accountName: [rules.required('请填写账户姓名'), { validator: validateName, trigger: 'blur' }],
        amount: [rules.required('请填写10-50000元金额'), rules.number('请输入大于10小于50000的正整数'), { validator: isAmount, trigger: 'blur' }]
      },
      accountData: [],
      isSave: true
    }
  },
  methods: {
    getOneInfo(id) {
      getAccountOne({ id: id }).then(res => {
        if (res.code === 200) {
          this.editAccount(res.data)
          this.$refs.account.resetFields()
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    getTableData() { // 获取店铺结算账户集合
      this.loading = true
      getAccountList().then(res => {
        if (res.code === 200) {
          // reverse()
          this.accountData = res.data
          this.formData.settlementId = res.data[0].id
          this.loading = false
          settleNumber().then(res => { // 判断账户是否达到上限
            if (res.code === 200) {
              this.isSave = res.data// 控制新增按钮
            } else {
              this.$errorMessage(res.message)
            }
          })
        } else {
          this.$errorMessage(res.message)
          this.loading = false
        }
      })
    },
    withdraw() { // 提现
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          var params = {
            payPassWord: this.$cms.encrypt(this.formData.payPassWord),
            settlementId: this.formData.settlementId,
            amount: this.formData.amount
          }
          storeAccountPresent(params).then(res => {
            if (res.code === 200) {
              this.$successMessage('提现成功')
              location.reload()
            } else {
              this.$errorMessage(res.message)
            }
          })
        }
      })
    },
    editAccount(data) {
      if (data.id && data.id !== '') {
        this.type = '修改'
        this.userInfo = data
      } else {
        this.type = '新增'
        this.userInfo = Object.assign({}, userData)
      }
      this.accountDialog = true
    },
    saveAccount() { // 提交
      this.$refs['account'].validate((valid) => {
        if (valid) {
          if (this.type === '新增') { // 新增操作
            saveSettleAccount(this.userInfo).then(res => {
              if (res.code === 200) {
                this.$successMessage('添加成功')
                this.getTableData()
                this.done()
              } else {
                this.$errorMessage(res.message)
              }
            })
          } else {
            updateSettleAccount(this.userInfo).then(res => { // 修改操作
              if (res.code === 200) {
                this.$successMessage('修改成功')
                this.getTableData()// 重新获取表单数据
                this.done()
              } else {
                this.$errorMessage(res.message)
              }
            })
          }
        } else {
          return false
        }
      })
    },
    done() {
      this.accountDialog = false
      this.$refs.account.resetFields()
      this.userInfo = Object.assign({}, userData)
    },
    activeTab(id) {
      this.formData.settlementId = id
    }
  },
  filters: {
    codeFilter(code) {
      if (parseInt(code) === 1) {
        return '支付宝'
      }
    }
  },
  created() {
    this.getTableData()
  }
}
</script>

<style lang="scss" scoped>
.user-account {
  &__list {
    width: 100%;
  }
  &__item {
    width: 190px;
    height: 65px;
    margin-right: 10px;
    margin-bottom: 10px;
    padding: 5px;
    box-sizing: border-box;
    border: 1px solid rgba(204, 204, 204, 1);
    border-radius: 3px;
    position: relative;
    display: inline-block;
    vertical-align: top;
    &--active{
      border: 1px solid rgba(78, 140, 238, 1);
    }
  }
  &__txt {
    line-height: 18px;
    font-size: 13px;
    color: #777;
  }
  &__defult {
    color: #fff;
    background-color: rgba(78, 140, 238, 1);
    border-radius: 3px;
    line-height: 18px;
    padding: 0 3px;
    font-size: 12px;
  }
  &__nomal {
    border: 1px dashed #ccc;
    color: #ccc;
    background-color: #eee;
    text-align: center;
    line-height: 56px;
    user-select: none;
    cursor: pointer;
  }
  &__add {
    font-size: 48px;
  }
}
</style>




