<template>
  <div>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>编号</label>
        <el-input v-model="queryParams.presentCode" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>状态</label>
        <el-select v-model="queryParams.status" @change="query" placeholder="请选择">
          <el-option label="全部" :value="''"></el-option>
          <el-option label="待审核" :value="1"></el-option>
          <el-option label="审核不通过" :value="2"></el-option>
          <el-option label="待转账" :value="3"></el-option>
          <el-option label="转账失败" :value="4"></el-option>
          <el-option label="完成" :value="5"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker="true" @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 数据列表 -->
    <el-table :data="tableData" style="width: 100%" stripe ref="dataTable">
      <el-table-column prop="presentCode" label="提现编号" min-width="160"></el-table-column>
      <el-table-column prop="money" label="提现金额" min-width="100" align="center">
        <div slot-scope="scope">{{$cms.float(scope.row.money)}}</div>
      </el-table-column>
      <el-table-column prop="tradeNo" label="交易流水号" min-width="260" align="center"></el-table-column>
      <el-table-column
        prop="status"
        label="状态"
        min-width="110"
        :formatter="typeFilter"
        align="center"
      ></el-table-column>
      <el-table-column prop="price" label="申请时间" min-width="160" align="center">
        <div slot-scope="scope">{{$moment(scope.row.createTime)}}</div>
      </el-table-column>
      <el-table-column prop="isShow" label="完成时间" min-width="160" align="center">
        <div slot-scope="scope">{{$moment(scope.row.finshTime)}}</div>
      </el-table-column>
      <el-table-column prop="remark" label="备注" min-width="160" align="center">
        <template slot-scope="scope">
          <div class="text-overflow" :title="scope.row.remark">{{scope.row.remark}}</div>
        </template>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total="total"></cms-pagination>
  </div>
</template>

<script>
var defaultData = {
  page: 1,
  size: 10,
  presentCode: '',
  status: ''
}
import list from '@/mixins/list'
import { storeAccountPresentpage } from '@/api/shop/shopAccount.js'
export default {
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    typeFilter(row, column) {
      var types = ['', '待审核', '审核不通过', '待转账', '转账失败', '完成']
      return types[parseInt(row.status)]
    },
    changeData() {
      // 通过ref来触发
      this.getTableData(storeAccountPresentpage, this.queryParams)
    }
  },
  activated() {
    this.getTableData(storeAccountPresentpage, this.queryParams)
  },
  created() {
    this.getTableData(storeAccountPresentpage, this.queryParams)
  }
}
</script>
<style lang="scss" scoped>
.cms-list-query {
  padding-top: 0;
  padding-bottom: 3px;
}
</style>



