<template>
  <div>
    <div>
      <cms-prompt collapse="true">
        <li>充值成功后，余额到账速度可能会受支付方式的不同产生1~30分钟的延时，请耐心等待，如长时间未到账，请联系网站客服</li>
        <li>充值金额必须大于10的整数</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px">
        <el-form-item label="充值账户">
          <p>{{$cms.float(price)}}（{{retype}}）</p>
        </el-form-item>
        <el-form-item label="充值金额" prop="amount">
          <el-input maxlength="6" v-model.number="formData.amount"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="recharge" :disabled="formData.amount<10" v-color>充值</cms-button>
      </div>
      <pay-box
        ref="pay"
        :payScene="'storeRecharge'"
        :orderNum="orderNum"
        :money="money"
        @change="getPay"
        v-if="payStatus"
      ></pay-box>
    </div>
  </div>
</template>

<script>
import rules from '@/rules'
import form from '@/mixins/form.js'
import { storeAccountRecharge } from './../../../../api/shop/shopAccount.js'
import payBox from './../../../../components/operation/pay'
var defaultData = {
  amount: ''
}
var isAmount = (rule, value, callback) => {
  if (parseInt(value) < 10) {
    callback(new Error('面值不能小于10'))
  } else {
    callback()
  }
}
export default {
  props: {
    retype: {
      type: String,
      default: ''
    },
    price: {
      type: [String, Number],
      default: 0.0
    }
  },
  mixins: [form],
  components: { payBox },
  data() {
    return {
      formData: Object.assign({}, defaultData),
      rules: {
        // 充值规则
        amount: [
          rules.required('请填写10元或10元以上金额'),
          rules.number('请输入大于10的正整数'),
          { validator: isAmount, trigger: 'blur' }
        ]
      },
      payStatus: false,
      orderNum: '',
      money: '',
      payScene: ''
    }
  },
  methods: {
    recharge() { // 店铺充值
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          storeAccountRecharge(this.formData).then(res => {
            if (res.code === 200) {
              this.orderNum = res.data.businessNo
              this.money = res.data.money
              this.payStatus = true
              this.$nextTick(() => {
                this.$refs.pay.showPay()
              })
            } else {
              this.$errorMessage(res.message)
            }
          })
        }
      })
    },
    getPay(res) {
      // 获取支付状态
      if (res) {
        location.reload()
      }
    }
  },
  created() {}
}
</script>



